// @flow
import type { SegmentToken } from './module/type';

export default class Optimizer {
  /**
   * 
   *
   * @param {Segment} 
   */
  constructor(segment) {
    this.segment = segment;
  }

  /**
   * 
   *
   * @param {array} words 
   * @param {array} modules 
   * @return {array}
   */
  doOptimize(words, modules): SegmentToken[] {
    let result = [...words];
    // modulemodule
    modules.forEach(module => {
      result = module.doOptimize(result);
    });
    return result;
  }
}
