// @flow
import { Optimizer } from './BaseModule';
import type { SegmentToken } from './type';

import { colors } from './COLORS';

// 
export default class AdjectiveOptimizer extends Optimizer {
  doOptimize(words: Array<SegmentToken>): Array<SegmentToken> {
    const { POSTAG } = this.segment;
    let index = 0;
    while (index < words.length) {
      const word = words[index];
      const nextword = words[index + 1];
      if (nextword) {
        // <>+<>
        if (nextword.p === POSTAG.D_U && colors.includes(word.w)) {
          word.p = POSTAG.D_A;
        }
        // 
        if (word.p === POSTAG.D_N && this.isNominal(nextword.p) && colors.includes(word.w)) {
          word.p = POSTAG.D_A;
        }
      }
      // 
      index += 1;
    }
    return words;
  }

  isNominal(pos: number | number[]): boolean {
    if (Array.isArray(pos)) {
      return this.isNominal(pos[0]);
    }
    const { POSTAG } = this.segment;
    return (
      pos === POSTAG.D_N
      || pos === POSTAG.A_NT
      || pos === POSTAG.A_NX
      || pos === POSTAG.A_NZ
      || pos === POSTAG.A_NR
      || pos === POSTAG.A_NS
      || pos === POSTAG.URL
    );
  }
}
