// @flow
import { Optimizer } from './BaseModule';
import type { SegmentToken } from './type';

import {
  FAMILY_NAME_1, FAMILY_NAME_2, SINGLE_NAME, DOUBLE_NAME_1, DOUBLE_NAME_2,
} from './CHS_NAMES';

export default class ChsNameOptimizer extends Optimizer {
  doOptimize(words: Array<SegmentToken>): Array<SegmentToken> {
    const POSTAG = this.segment.POSTAG;
    let i = 0;

    /*  */
    while (i < words.length) {
      const word = words[i];
      const nextword = words[i + 1];
      if (nextword) {
        // debug(nextword);
        //   "|" + 
        if (
          nextword
          && (word.w === '' || word.w === '')
          && (nextword.w in FAMILY_NAME_1 || nextword.w in FAMILY_NAME_2)
        ) {
          words.splice(i, 2, {
            w: word.w + nextword.w,
            p: POSTAG.A_NR,
          });
          i++;
          continue;
        }

        //   + 2
        if (
          (word.w in FAMILY_NAME_1 || word.w in FAMILY_NAME_2)
          && ((nextword.p & POSTAG.A_NR) > 0 && nextword.w.length <= 2)
        ) {
          words.splice(i, 2, {
            w: word.w + nextword.w,
            p: POSTAG.A_NR,
          });
          i++;
          continue;
        }

        // 
        if (!word.p || !nextword.p) {
          if (
            (word.w in SINGLE_NAME && word.w === nextword.w)
            || (word.w in DOUBLE_NAME_1 && nextword.w in DOUBLE_NAME_2)
          ) {
            words.splice(i, 2, {
              w: word.w + nextword.w,
              p: POSTAG.A_NR,
            });
            // 
            const preword = words[i - 1];
            if (preword && (preword.w in FAMILY_NAME_1 || preword.w in FAMILY_NAME_2)) {
              words.splice(i - 1, 2, {
                w: preword.w + word.w + nextword.w,
                p: POSTAG.A_NR,
              });
            } else {
              i++;
            }
            continue;
          }
        }

        //   + 2 
        if ((word.w in FAMILY_NAME_1 || word.w in FAMILY_NAME_2) && (!word.p || !nextword.p)) {
          // debug(word, nextword);
          words.splice(i, 2, {
            w: word.w + nextword.w,
            p: POSTAG.A_NR,
          });
        }
      }

      // 
      i++;
    }

    /*  */
    i = 0;
    while (i < words.length) {
      const word = words[i];
      const nextword = words[i + 1];
      if (nextword) {
        //   + 
        if ((word.w in FAMILY_NAME_1 || word.w in FAMILY_NAME_2) && nextword.w in SINGLE_NAME) {
          words.splice(i, 2, {
            w: word.w + nextword.w,
            p: POSTAG.A_NR,
          });
          i++;
          continue;
        }
      }

      // 
      i++;
    }

    return words;
  }
}
