import POSTAG from './POSTAG';
import Tokenizer from './Tokenizer';
import Optimizer from './Optimizer';

/**
 * 
 */
export default class Segment {
  constructor() {
    this.POSTAG = POSTAG; // 
    this.DICT = {}; // 
    this.modules = {
      tokenizer: [], // 
      optimizer: [], // 
    };
    this.tokenizer = new Tokenizer(this);
    this.optimizer = new Optimizer(this);
  }

  /**
   * 
   *
   * @param {String|Array|Object} module ()
   * @return {Segment}
   */
  use = (Module: Array | Object): Segment => {
    // 
    if (Array.isArray(Module)) {
      Module.forEach(this.use);
    } else {
      // 
      if (typeof Module.init === 'function') {
        Module.init(this);
        this.modules[Module.type].push(Module);
      } else {
        const module = new Module(this);
        this.modules[module.type].push(module);
      }
    }

    return this;
  };

  /**
   * 
   *
   * @param {String} name 
   * @param {String} type 
   * @param {Boolean} convertToLower 
   * @return {Segment}
   */
  loadDict = (
    dict: string | string[],
    type = 'TABLE',
    convertToLower: boolean = false,
  ): Segment => {
    if (Array.isArray(dict)) {
      dict.forEach(d => this.loadDict(d));
    } else {
      // 
      if (!this.DICT[type]) this.DICT[type] = {};
      if (!this.DICT[`${type}2`]) this.DICT[`${type}2`] = {};
      const TABLE = this.DICT[type]; //   '' => {}
      const TABLE2 = this.DICT[`${type}2`]; //   '' => '' => 
      // 
      dict
        .split(/\r?\n/)
        .map(line => {
          if (convertToLower) return line.toLowerCase();
          return line;
        })
        .forEach(line => {
          const blocks = line.split('|');
          if (blocks.length > 2) {
            const w = blocks[0].trim();
            const p = Number(blocks[1]);
            const f = Number(blocks[2]);

            // Bug
            if (w.length > 0) {
              TABLE[w] = { f, p };
              if (!TABLE2[w.length]) TABLE2[w.length] = {};
              TABLE2[w.length][w] = TABLE[w];
            }
          }
        });
    }

    return this;
  };

  /**
   * 
   *
   * @param {String} type 
   * @return {object}
   */
  getDict = (type: string) => this.DICT[type];

  /**
   * 
   *
   * @param {Object} dict 
   */
  loadSynonymDict = (dict: string | string[]): Segment => {
    if (Array.isArray(dict)) {
      dict.forEach(d => this.loadSynonymDict(d));
    } else {
      const type = 'SYNONYM';

      // 
      if (!this.DICT[type]) this.DICT[type] = {};
      const TABLE = this.DICT[type]; //   '' => ''
      // 
      dict
        .split(/\r?\n/)
        .map(line => line.split(','))
        .forEach(blocks => {
          if (blocks.length > 1) {
            const n1 = blocks[0].trim();
            const n2 = blocks[1].trim();
            TABLE[n1] = n2;
            if (TABLE[n2] === n1) {
              delete TABLE[n2];
            }
          }
        });
    }

    return this;
  };

  /**
   * 
   *
   * @param {Object} dict 
   */
  loadStopwordDict = (dict: string | string[]): Segment => {
    if (Array.isArray(dict)) {
      dict.forEach(d => this.loadStopwordDict(d));
    } else {
      const type = 'STOPWORD';

      // 
      if (!this.DICT[type]) this.DICT[type] = {};
      const TABLE = this.DICT[type]; //   '' => ''
      // 
      dict
        .split(/\r?\n/)
        .map(line => line.trim())
        .forEach(line => {
          if (line) {
            TABLE[line] = true;
          }
        });
    }

    return this;
  };

  /**
   * 
   *
   * @param {String} text 
   * @param {Object} options 
   *   - {Boolean} simple 
   *   - {Boolean} stripPunctuation 
   *   - {Boolean} convertSynonym 
   *   - {Boolean} stripStopword 
   * @return {Array}
   */
  doSegment = (text, options) => {
    const me = this;
    options = options || {};
    let ret = [];

    // 
    text
      .replace(/\r/g, '\n')
      // 
      .split(/\n+/)
      .forEach(section => {
        var section = section.trim();
        if (section.length < 1) return;
        // ======================================
        // 
        let sret = me.tokenizer.split(section, me.modules.tokenizer);

        // 
        sret = me.optimizer.doOptimize(sret, me.modules.optimizer);

        // ======================================
        // 
        if (sret.length > 0) ret = ret.concat(sret);
      });

    // 
    if (options.stripPunctuation) {
      ret = ret.filter(item => item.p !== POSTAG.D_W);
    }

    // 
    function convertSynonym(list) {
      let count = 0;
      const TABLE = me.getDict('SYNONYM');
      list = list.map(item => {
        if (item.w in TABLE) {
          count++;
          return { w: TABLE[item.w], p: item.p };
        }
        return item;
      });
      return { count, list };
    }
    if (options.convertSynonym) {
      do {
        var result = convertSynonym(ret);
        ret = result.list;
      } while (result.count > 0);
    }

    // 
    if (options.stripStopword) {
      const STOPWORD = me.getDict('STOPWORD');
      ret = ret.filter(item => !(item.w in STOPWORD));
    }

    // 
    if (options.simple) {
      ret = ret.map(item => item.w);
    }

    return ret;
  };

  /**
   * 
   *
   * @param {Array} words 
   * @return {String}
   */
  toString(words) {
    return words.map(item => item.w).join('');
  }

  /**
   * 
   *
   * @param {Array} words 
   * @param {Number|String} s 
   * @return {Array}
   */
  split(words, s) {
    const ret = [];
    let lasti = 0;
    let i = 0;
    const f = typeof s === 'string' ? 'w' : 'p';

    while (i < words.length) {
      if (words[i][f] === s) {
        if (lasti < i) ret.push(words.slice(lasti, i));
        ret.push(words.slice(i, i + 1));
        i++;
        lasti = i;
      } else {
        i++;
      }
    }
    if (lasti < words.length - 1) {
      ret.push(words.slice(lasti, words.length));
    }

    return ret;
  }

  /**
   * 
   *
   * @param {Array} words 
   * @param {Number|String} s 
   * @param {Number} cur 
   * @return {Number} -1
   */
  indexOf(words, s, cur) {
    cur = isNaN(cur) ? 0 : cur;
    const f = typeof s === 'string' ? 'w' : 'p';

    while (cur < words.length) {
      if (words[cur][f] === s) return cur;
      cur++;
    }

    return -1;
  }
}
