// @flow
import { Optimizer } from './BaseModule';
import type { SegmentToken } from './type';

export default class DictOptimizer extends Optimizer {
  /**
   * 
   *
   * @param {array} words 
   * @param {bool} isNotFirst 
   * @return {array}
   */
  doOptimize(words: Array<SegmentToken>, isNotFirst: boolean): Array<SegmentToken> {
    // debug(words);
    if (typeof isNotFirst === 'undefined') {
      isNotFirst = false;
    }
    // 
    const TABLE = this.segment.getDict('TABLE');
    const POSTAG = this.segment.POSTAG;

    let i = 0;
    let ie = words.length - 1;
    while (i < ie) {
      const w1 = words[i];
      const w2 = words[i + 1];
      // debug(w1.w + ', ' + w2.w);

      // ==========================================
      // ()
      const nw = w1.w + w2.w;
      if (w1.p === w2.p && nw in TABLE) {
        words.splice(i, 2, {
          w: nw,
          p: TABLE[nw].p,
        });
        ie--;
        continue;
      }

      //  +  =   +  = 
      if ((w1.p & POSTAG.D_A) > 0 && w2.p & POSTAG.D_U) {
        words.splice(i, 2, {
          w: nw,
          p: POSTAG.D_A,
        });
        ie--;
        continue;
      }

      // ============================================
      // 
      if ((w1.p & POSTAG.A_M) > 0) {
        // debug(w2.w + ' ' + (w2.p & POSTAG.A_M));
        //   10%
        if ((w2.p & POSTAG.A_M) > 0 || w2.w === '%') {
          words.splice(i, 2, {
            w: w1.w + w2.w,
            p: POSTAG.A_M,
          });
          ie--;
          continue;
        }
        //  +  100
        if ((w2.p & POSTAG.A_Q) > 0) {
          words.splice(i, 2, {
            w: w1.w + w2.w,
            p: POSTAG.D_MQ, // 
          });
          ie--;
          continue;
        }
        //   3 . 14 
        //  + "" + 
        var w3 = words[i + 2];
        if (w3 && (w3.p & POSTAG.A_M) > 0 && (w2.w === '.' || w2.w === '' || w2.w === '')) {
          words.splice(i, 3, {
            w: w1.w + w2.w + w3.w,
            p: POSTAG.A_M,
          });
          ie -= 2;
          continue;
        }
      }

      //  
      if ((w1.p & POSTAG.D_MQ) > 0 && w1.w.substr(-1) === '' && w2.p & POSTAG.A_M) {
        // debug(w1, w2);
        let i2 = 2;
        let w4w = '';
        for (let j = i + i2; j < ie; j++) {
          var w3 = words[j];
          if ((w3.p & POSTAG.A_M) > 0) {
            w4w += w3.w;
            i2++;
          } else {
            break;
          }
        }
        words.splice(i, i2, {
          w: w1.w + w2.w + w4w,
          p: POSTAG.D_MQ, // 
        });
        ie -= i2 - 1;
        continue;
      }

      // 
      i++;
    }

    // 
    return isNotFirst === true ? words : this.doOptimize(words, true);
  }
}
